html, body, #root {
  color: #fff;
  background: #000;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, sans-serif;
  overflow: hidden;
}

.container {
  scroll-snap-type: y mandatory;
  scrollbar-width: none;
  -ms-overflow-style: none;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
}

.container::-webkit-scrollbar {
  display: none;
}

.top-bar {
  backdrop-filter: blur(5px);
  z-index: 100;
  background: #000c;
  justify-content: space-between;
  align-items: center;
  height: 3.5rem;
  padding: 0 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.logo {
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  font-weight: bold;
}

.dot-xyz {
  color: #fe9971;
  font-size: clamp(.8rem, 2vw, 1rem);
  font-weight: bold;
}

.category-select {
  color: #fff;
  background: #000;
  border: 1px solid #555;
  padding: .3rem .5rem;
  font-size: clamp(.85rem, 3vw, 1rem);
}

.card {
  scroll-snap-align: start;
  background-color: #111;
  background-position: center;
  background-repeat: no-repeat;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
  display: flex;
  position: relative;
  background-size: cover !important;
}

.card-content {
  backdrop-filter: blur(10px);
  box-sizing: border-box;
  z-index: 2;
  background: #0009;
  margin: 0 auto clamp(2.5rem, 7vh, 3.75rem);
  padding: 1.75rem 1.25rem 2.75rem;
  position: relative;
}

.card-content-inner {
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

h1 {
  margin-bottom: .5rem;
  font-size: clamp(1.25rem, 5vw, 2rem);
  font-weight: bold;
}

p {
  -webkit-line-clamp: 4;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  margin: 0;
  font-size: clamp(.9rem, 3.5vw, 1.05rem);
  line-height: 1.6;
  display: -webkit-box;
  overflow: hidden;
}

.read-more {
  color: #ccc;
  margin-top: .8rem;
  font-size: clamp(.85rem, 3vw, .95rem);
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.loading {
  color: #fff;
  text-align: center;
  margin-top: 6rem;
  padding: 2rem;
  font-size: 1.2rem;
}

.card-header {
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.bookmark-btn {
  color: #ccc;
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  font-size: 2.5rem;
  transition: color .2s, transform .2s;
}

.bookmark-btn:hover {
  color: #fff;
  transform: scale(1.1);
}

.bookmark-btn.active {
  color: #4ea1ff;
}

.modal-overlay {
  backdrop-filter: blur(4px);
  z-index: 200;
  background: #000000d9;
  justify-content: center;
  align-items: flex-start;
  padding-top: 4rem;
  display: flex;
  position: fixed;
  inset: 0;
  overflow-y: auto;
}

.modal {
  color: #fff;
  background: #111;
  border-radius: .75rem;
  width: 90%;
  max-width: 720px;
  padding: 1.5rem;
  box-shadow: 0 0 20px #0009;
}

.modal-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
}

.close-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.5rem;
}

.search-input {
  color: #fff;
  background: #222;
  border: 1px solid #444;
  border-radius: .4rem;
  width: 80%;
  margin: 1rem auto 1.25rem;
  padding: .5rem .75rem;
  font-size: 1rem;
  display: block;
}

.bookmark-list {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.bookmark-item {
  background: #1a1a1a;
  border-radius: .5rem;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.bookmark-thumb {
  object-fit: cover;
  border-radius: .25rem;
  flex-shrink: 0;
  width: 64px;
  height: 64px;
}

.bookmark-info {
  flex: 1;
  overflow: hidden;
}

.bookmark-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: .3rem;
  font-size: 1rem;
  font-weight: bold;
  overflow: hidden;
}

.bookmark-desc {
  color: #ccc;
  text-overflow: ellipsis;
  max-height: 3.5em;
  font-size: .9rem;
  overflow: hidden;
}

.bookmark-remove {
  color: #888;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.5rem;
  transition: color .2s;
}

.bookmark-remove:hover {
  color: #f44;
}

.empty {
  text-align: center;
  color: #777;
  padding: 1rem;
}

.top-controls {
  align-items: center;
  gap: .75rem;
  display: flex;
}

.bookmark-open-btn {
  color: #fff;
  cursor: pointer;
  background: #000;
  border: 1px solid #555;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: .3rem .55rem;
  font-size: 1.2rem;
  transition: background .2s, border .2s;
  display: flex;
}

.bookmark-open-btn:hover {
  background: #111;
  border-color: #888;
}
